export class Job {
            id : string = '';
            title : string = '';
            description : string = '';
            category_id : number = 0;
            company_name : string = '';
            company_website : string = '';
            company_email : string = '';
            company_phone : string = '';
            company_logo : string = '';
            company_facebook : string = '';
            company_video : string = '';
            keywords : string = '';
            type : string = '';
            requirements : string = '';
            updated_at : string = '';
            user_id : number = 0;
            finish : string = '';
            created_at : string = '';
            city : string = '';
            district : string = '';
            country : string = 'Nepal';
            zone : string = '';
            filled : boolean = false;
            applicant_count: number = 0;
            category: any;
}


